






























































































































































































































































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast, Dialog } from "vant";

// import { recommendList } from '../../service/getData';
import comhead from "../../components/Comhead.vue";

@Component({
  components: { comhead },
  mixins: [mixin]
})
export default class orderdetail extends Vue {
  orderId = "";
  detail = {};


  inputTransNo() {
    console.log("填写单号");
    this.$router.push({
      name: "refundbackgoods",
      query: {
        refundId: this.detail["detailList"][0].refundOrderList[0].refundId
      }
    });
  }
  formatButtonColor() {
    return "border-color:#ffc630;color:#ffc630";
  }
  gocomment() {
    this.$router.push({
      name: "addcomment",
      query: {
        orderId: this.detail["orderId"]
      }
    });
  }
  buyAgain() {
    Vue.prototype.$reqFormPost(
      "/order/buyagain",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        orderId: this.orderId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.$router.push({ name: "cart" });
      }
    );
  }
  recvgoods(orderId,ORDER_WAIT_REVIEW) {
    Dialog.confirm({
      title: "提示",
      message: "确认收货?"
    })
      .then(() => {
        Vue.prototype.$reqFormPost(
          "/order/recvgoods",
          {
            userId: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].userId,
            token: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].token,
            orderId: orderId
          },
          res => {
            if (res == null) {
              console.log("网络请求错误！");
              return;
            }
            if (res.data.status != 200) {
              console.log(
                "需控制错误码" +
                  res.data.status +
                  ",错误信息：" +
                  res.data.message
              );
              Toast(res.data.message);
              return;
            }
            this.$router.push({
                name: "orderlist",
            });

            // add(ORDER_WAIT_REVIEW){
            Vue.prototype.$confirmLogin({
              name: "orderlist",
              query: {
                orderStatus: "ORDER_WAIT_REVIEW"
              }
            });
            // }
            this.queryDetail();
          }
        );
        // on confirm
      })
      .catch(() => {
        // on cancel
      });
  }
  getShip() {
    this.$router.push({ name: "ship", query: this.detail });
  }
  formatStatus(status) {
    // ORDER_WAIT_PAY
    // ORDER_CANCEL_PAY
    // ORDER_WAIT_SENDGOODS
    // ORDER_WAIT_RECVGOODS
    // ORDER_WAIT_REVIEW
    // ORDER_END_GOODS
    // ORDER_FINISH
    switch (status) {
      case "ORDER_WAIT_PAY":
        return "未付款";
      case "ORDER_CANCEL_PAY":
        return "已取消";
      case "ORDER_WAIT_SENDGOODS":
        return "未发货";
      case "ORDER_WAIT_RECVGOODS":
        return "未收货";
      case "ORDER_END_GOODS":
        return "已评论";
      case "ORDER_WAIT_REVIEW":
        return "待评价";
      case "ORDER_FINISH":
        return "交易完成";
    }
  }
  formatStatusColor(status) {
    switch (status) {
      case "ORDER_WAIT_SENDGOODS":
        return "color:red";
      case "ORDER_WAIT_RECVGOODS":
        return "color:red";
      case "ORDER_CANCEL_PAY":
        return "color:red";
      case "ORDER_WAIT_PAY":
        return "color:red";
      case "ORDER_END_GOODS":
        return "color:#ffc630";

      case "ORDER_WAIT_REVIEW":
        return "color:#ffc630";
      case "ORDER_FINISH":
        return "color:#ffc630;";
    }
  }
  doCancel() {
    Dialog.confirm({
      title: "提示",
      message: "是否取消订单?"
    })
      .then(() => {
        Vue.prototype.$reqFormPost(
          "/order/cancel",
          {
            userId: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].userId,
            token: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].token,
            orderId: this.detail["orderId"]
          },
          res => {
            if (res == null) {
              console.log("网络请求错误！");
              return;
            }
            if (res.data.status != 200) {
              console.log(
                "需控制错误码" +
                  res.data.status +
                  ",错误信息：" +
                  res.data.message
              );
              Toast(res.data.message);
              return;
            }
            this.queryDetail();

            console.log("取消订单");
          }
        );
        // on confirm
      })
      .catch(() => {
        // on cancel
      });
  }
  cancelRefund() {
    Dialog.confirm({
      title: "提示",
      message: "是否取消申请退款?"
    })
      .then(() => {
        Vue.prototype.$reqFormPost(
          "/order/refund/delete",
          {
            userId: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].userId,
            token: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
            ].token,
            refundId: this.detail["detailList"][0].refundOrderList[0].refundId
          },
          res => {
            if (res == null) {
              console.log("网络请求错误！");
              return;
            }
            if (res.data.status != 200) {
              console.log(
                "需控制错误码" +
                  res.data.status +
                  ",错误信息：" +
                  res.data.message
              );
              Toast(res.data.message);
              return;
            }
            Toast("取消成功");
            this.queryDetail();
          }
        );
        // on confirm
      })
      .catch(() => {
        // on cancel
      });

    console.log("取消退款");
  }
  queryDetail() {
    Vue.prototype.$reqFormPost(
      "/order/queryorderdetail",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        orderId: this.orderId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.detail = res.data.data;
        console.log(res.data.data);
      }
    );
  }

  doRefund() {

    this.$router.push({
      name: "refund",
      query: {
        orderId: this.detail["orderId"]
      }
    });
  }

  payOrder() {
    this.$router.push({
      name: "pay",
      query: {
        body: this.detail["orderTitle"],
        payId: this.detail["payNo"],
        payTotal: this.detail["payTotal"]
      }
    });
  }
  mounted() {
    this.orderId = this.$route.query.orderId;
    this.queryDetail();
  }
}



